<template>
  <div class="page content">
    <h1>О себе</h1>
    <p>Привет! Меня зовут Павел Горшков. Мне {{year}} лет. Я занимаюсь FrontEnd разработкой <u>(основная занятость)</u> и немного BeckEnd разработкой <u>(реализации REST API на фреймворке Laravel для тренеровок по написанию кода FrontEnd)</u>.</p>
    <p>Мое хобби - это и есть программирование. Работа по этой профессии для меня огромное удовольствие.</p>
  </div>
</template>

<script>
export default {
  name: 'Hello',
  data () {
    return {
      year: new Date().getFullYear() - 1990
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  h1{
    margin-bottom: 20px;
  }
  p{
    font-size: calc(.8em + 1vw);
    line-height: calc(.8em + 1vw);
  }
  u {
    font-size: .6em;
  }
</style>



